.app {
  max-width: 1024px;
  margin: 0 auto;
  padding: 30px;
  font-family: Arial, sans-serif;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.tabs button {
  margin-right: 10px;
  padding: 5px 10px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  cursor: pointer;
}

.tabs button.active {
  background-color: #007bff;
  color: white;
}

.buttons {
  opacity: 0.7;
}

.buttons button {
  margin-left: 10px;
  padding: 5px 10px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  cursor: pointer;
}

.content {
  display: flex;
  flex-direction: column;
}

.section-card {
  margin-bottom: 20px;
  padding: 15px;
  border-radius: 8px;
}

.treatments-card { background-color: #e6f3ff; }
.add-ons-card { background-color: #f3e6ff; }
.bonuses-card { background-color: #fff3e6; }

.row-headers, .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.row-headers > div,
.row > * {
  flex: 1;
  padding: 0 5px;
}

.header-treatment-name,
.treatment-name { flex: 3; }

.header-price,
.price,
.header-count,
.count,
.header-total,
.total { 
  flex: 1; 
  max-width: 80px;
  text-align: right;
}

select, input[type="number"] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

@media (max-width: 768px) {
  .app {
    padding: 10px;
  }
}














